:root {
  --dark-spring-green: #02733e;
  --xanthous: #f2c36b;
  --ou-crimson: #880d1e;
  --coral: #f2784b;
  --cardinal: #bf213e;
  --lg-font: 3rem;
  --md-font: 1.6rem;
  --sm-font: 1.4rem;
  --quicksand-font: "Quicksand", serif;
  --poppins-font: "Poppins", serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  font-family: var(--quicksand-font);
  min-width: 32rem;
}

header {
  width: 100%;
  height: 66.7rem;
}

header .title {
  width: 100%;
  height: 80%;
  margin-bottom: 2rem;
}

header .title .title__heading {
  background-image: url("https://i.ibb.co/WZfwCpM/5d5284d1b0547c4867a37242da6736bf.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom-right-radius: .88rem;
  border-bottom-left-radius: .88rem;
  justify-content: flex-start;
  align-items: flex-end;
  width: 100%;
  max-width: 90rem;
  height: 60%;
  margin: 0 auto 1rem;
  padding: 1rem;
  display: flex;
}

header .title .title__heading--title {
  font-size: var(--lg-font);
  color: var(--coral);
}

header .title__subheading {
  text-align: center;
  width: 100%;
  max-width: 90rem;
  color: var(--ou-crimson);
  margin: 2rem auto;
  padding: 0 1.5rem;
  font-weight: 500;
}

header .title .title__tradition-link {
  width: 90%;
  max-width: 90rem;
  height: 13rem;
  margin: 0 auto 1rem;
}

header .title .title__tradition-link a {
  width: 100%;
  height: 100%;
  font-size: var(--md-font);
  color: #fff;
  background-image: url("https://i.ibb.co/zWcDFdZk/90613eac950fb8875868305e2f85863e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: .88rem;
  align-items: end;
  padding: 1rem;
  text-decoration: none;
  display: flex;
}

header .title .title__tradition-link a:hover {
  cursor: pointer;
}

header .title .title__tradition-link a span {
  background-color: var(--xanthous);
  color: #fff;
  border-radius: .88rem;
  padding: .3rem;
  display: inline-block;
}

header .buttons {
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  max-width: 32rem;
  margin: 0 auto;
  display: flex;
}

header .buttons .buttons__button-container {
  background-color: var(--xanthous);
  border-radius: .88rem;
  width: 12rem;
  height: 6rem;
  margin: 0 auto;
}

header .buttons .buttons__button-container a {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: flex;
}

header .buttons .buttons__button-container a span {
  color: #fff;
  font-size: var(--md-font);
  font-weight: 700;
}

main .menu {
  background-color: var(--dark-spring-green);
  width: 100%;
  height: auto;
}

main .menu .menu__blanck-space {
  width: 100%;
  height: 10rem;
}

main .menu .menu__title {
  width: 90%;
  max-width: 90rem;
  height: 3rem;
  margin: 0 auto 1rem;
  display: flex;
}

.menu__title .menu__heading {
  width: 34%;
  height: 100%;
  font-size: var(--lg-font);
  color: var(--xanthous);
  text-align: center;
}

.menu__title .menu__lateral-line {
  width: 33%;
  height: 100%;
}

.menu__title .menu__lateral-line--box1 {
  border-bottom: .1rem solid #000;
  justify-self: center;
  width: 50%;
  height: 50%;
}

.menu .menu__subtext {
  text-align: center;
  width: 90%;
  max-width: 90rem;
  color: var(--coral);
  font-size: var(--md-font);
  margin: 0 auto 2rem;
  font-weight: 500;
}

main .menu .menu__cards {
  flex-wrap: wrap;
  gap: 2rem;
  width: 90%;
  max-width: 90rem;
  margin: 0 auto;
  display: flex;
}

main .menu .menu__cards .menu__cards--card {
  background-color: var(--cardinal);
  border-radius: .88rem;
  width: 90%;
  max-width: 27rem;
  height: 30rem;
  margin: 0 auto 1rem;
  box-shadow: 0 4px 8px #0000004d, 0 2px 4px #bf213e33;
}

.menu__cards .menu__cards--card .card-image {
  border-radius: .88rem;
  width: 100%;
  height: 70%;
  margin-bottom: 1rem;
}

.menu__cards--card .card-image .card-image__option-image {
  object-fit: cover;
  border-radius: .88rem;
  width: 100%;
  max-width: 100%;
  height: 100%;
}

.menu__cards--card .card-description {
  justify-content: space-between;
  padding: 0 1rem;
  display: flex;
}

.card-description .card-description__text {
  color: #fff;
  font-weight: 300;
  font-size: var(--sm-font);
}

.card-description .card-description__button {
  background-color: var(--xanthous);
  border-radius: .88rem;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 3rem;
  margin: auto 0;
  display: flex;
}

.card-description .card-description__button a {
  cursor: pointer;
  color: #fff;
  width: 100%;
  height: 100%;
  text-decoration: none;
}

.card-description .card-description__button a span {
  font-size: 1.3rem;
  font-weight: 700;
}

main .contact {
  background-image: url("https://images.rawpixel.com/image_800/cHJpdmF0ZS9sci9pbWFnZXMvd2Vic2l0ZS8yMDIzLTEwL2FuZ3VzdGVvd19waG90b19vZl9iZWF1dGlmdWxfd2hpdGVfc25vd3lfd2l0aF9jaHJpc3RtYXNfdHJlZV9hbl8zMjhjMjMwMy1kYmVjLTQwNTctOWFhYi1mMGRlZWUwYzRkM2ZfMS5qcGc.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: auto;
  padding: 3rem 0;
}

main .contact .contact__photo-container, main .contact-card {
  width: 90%;
  max-width: 50rem;
  margin: 0 auto 2rem;
}

.contact .contact__photo-container--image {
  border-radius: .88rem;
  width: 100%;
  max-width: 27rem;
  height: 30rem;
  margin: 0 auto 1rem;
}

.contact .contact__photo-container--image img {
  object-fit: cover;
  border-radius: .88rem;
  width: 100%;
  max-width: 100%;
  height: 100%;
}

.contact h2 {
  text-align: center;
  color: var(--cardinal);
}

.contact-card__social-media {
  grid-template-columns: repeat(8, 1fr);
  gap: 1rem;
  width: 100%;
  max-width: 40rem;
  margin: 0 auto;
  display: grid;
  position: relative;
}

.contact-card__social-media__item {
  aspect-ratio: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.whatsapp-link {
  grid-area: 1 / 3 / span 4 / span 4;
}

.telegram-link {
  grid-area: 5 / 4 / span 2 / span 2;
}

.facebook-link {
  grid-area: 5 / 2 / span 2 / span 2;
}

.instagram-link {
  grid-area: 5 / 6 / span 2 / span 2;
}

.contact-card__social-media__item img, .contact-card__social-media__item a {
  width: 100%;
  height: 100%;
  position: absolute;
}
